import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { Container, Row, Col, Form } from 'react-bootstrap';
import CustomerList from './components/CustomerList';
import TransactionChart from './components/TransactionChart';

function App() {
  const [customers, setCustomers] = useState([]);
  const [transactions, setTransactions] = useState([]);
  const [filteredTransactions, setFilteredTransactions] = useState([]);
  const [selectedCustomer, setSelectedCustomer] = useState(null);
  const [filterName, setFilterName] = useState('');
  const [filterAmount, setFilterAmount] = useState('');

  useEffect(() => {
    axios.get('http://localhost:3000/customers') //!==> do not forget to replace your localhost link 
      .then(response => setCustomers(response.data))
      .catch(error => console.error('Error fetching customers', error));

    axios.get('http://localhost:3000/transactions') //!=> do not forget to replace your localhost link
      .then(response => {
        setTransactions(response.data);
        setFilteredTransactions(response.data);
      })
      .catch(error => console.error('Error fetching transactions', error));
  }, []);

  const handleFilter = () => {
    let filtered = transactions;

    if (filterName.trim() !== '') {
      filtered = filtered?.filter(transaction => {
        const customer = customers.find(c => c.id === transaction.customerId);
        return customer.name.toLowerCase().includes(filterName.toLowerCase());
      });
    }

    if (filterAmount.trim() !== '') {
      const amount = parseFloat(filterAmount);
      if (!isNaN(amount)) {
        filtered = filtered.filter(transaction => transaction.amount === amount);
      }
    }

    setFilteredTransactions(filtered);
  };

  const handleCustomerSelect = (customerId) => {
    setSelectedCustomer(customerId);
  };

  return (
    <div className="App">
      <Container className="mt-4">
        <h1 className="text-center mb-4 border bg-dark-subtle rounded p-3">Customer Transaction Dashboard</h1>
        <Row>
          <Col md={6}>
            <Form>
              <Form.Group controlId="filterName" className='mb-3'>
                <Form.Label>Filter by Customer Name</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="Enter customer name"
                  value={filterName}
                  onChange={(e) => setFilterName(e.target.value)}
                />
              </Form.Group>
              <Form.Group controlId="filterAmount">
                <Form.Label>Filter by Transaction Amount</Form.Label>
                <Form.Control
                  type="number"
                  placeholder="Enter transaction amount"
                  value={filterAmount}
                  onChange={(e) => setFilterAmount(e.target.value)}
                />
              </Form.Group>
              <button type="button" className="btn btn-primary mt-3" onClick={handleFilter}>
                Apply Filters
              </button>
            </Form>
            <CustomerList
              customers={customers}
              transactions={filteredTransactions}
              onCustomerSelect={handleCustomerSelect}
            />
          </Col>
          <Col md={6}>
            {selectedCustomer && (
              <TransactionChart
                transactions={filteredTransactions.filter(t => t.customerId === selectedCustomer)}
              />
            )}
          </Col>
        </Row>
      </Container>
    </div>
  );
}

export default App;


